@font-face {
  font-family: "Cormorant Garamond";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src:
    local("Cormorant Garamond SemiBold"),
    local("CormorantGaramond-SemiBold"),
    url("../fonts/cormorant-garamond-600.woff2") format("woff2"),
    url("../fonts/cormorant-garamond-600.woff") format("woff");
}

